<template>
<section class="paying">
    <div class="paying__top-block">
        <h2 class="paying__top-block--heading">Проведите оплату</h2>
    </div>
    <div class="paying__pay-block">
        <div class="paying__pay-block--main-info">

            <div class="paying__pay-block--info-pay">
                <img class="paying__pay-block--info-pay-imgbank" :src=getImgUrl(orderdata.src1) alt="">
                <span class="paying__pay-block--info-pay-namebank">{{orderdata.text1}}</span>
            </div>
            <div class="paying__pay-block--info-data">
                <span class="paying__pay-block--info-data-adress">Адрес: {{orderdata.address}}</span>
                <span class="paying__pay-block--info-data-sum">Сумма: {{ orderdata.giving}} {{orderdata.utf}}</span>
            </div>

        </div>
        <img :src=getImgUrl(orderdata.qr) class="paying__pay-block--qr" alt="">

    </div>
    <div class="paying__timer" id="timer">15:00</div>
    <div class="paying__btn" @click="editpath()">Я оплатил</div>

</section>
</template>

<script>
import {
    mapGetters
} from 'vuex';

export default {
    computed: {
        ...mapGetters([
            'orderdata'
        ])
    },
    mounted() {
        this.timer()
    },
    methods: {
        getImgUrl(pic) {
            return require('../assets/' + pic);
        },
        editpath() {
            this.$store.dispatch('editpath', 'step4');
        },
        timer() {
            var second = 10;
            var minute = 15;

            setInterval(function () {
                document.getElementById("timer").innerHTML = minute + ":" + second;

                second--;

                if (second < 10) {
                    second = '0' + second
                }
                if (second == 0) {
                    minute--;
                    second = 59;
                }
                if (minute == 0 && second == 1) {
                    document.getElementById("timer").innerHTML = "Timer Stopped";
                }
                if (minute <= -1) {
                    document.getElementById("timer").innerHTML = " ";
                }

            }, 1000);

        }
    }
}
</script>

<style lang="scss" scoped>
.paying {
    &__timer {
        width: 150px;
        text-align: left !important;
        padding-left: calc(50% - 75px);
        padding-top: 40px;
        padding-bottom: 40px;
        color: black;
        font-weight: bold;
        font-size: 80px;
    }

    &__btn {
        font-size: 20px;
        font-weight: 500;

        &:hover {
            cursor: pointer;
        }
    }

    padding: 20px 3%;
    font-family: 'Hind Siliguri',
    sans-serif;
    width: 94%;
    background: white;
    box-shadow: 0 2px 4px #c4c2c2;
    border-radius: 4px;
    display: flex;
    flex-direction: column;

    &__top-block {
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        &--heading {
            padding-left: 10px;
            font-size: 25px;
            color: black;
            font-weight: bold;
            margin: 0;
        }

    }

    &__pay-block {
        &--qr {
            width: 100px;
            height: 100%;
        }

        margin-top: 40px;
        margin-bottom: 5%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        &--main-info {
            font-family: 'Hind Siliguri', sans-serif;
            padding: 5px;
            width: 100%;
            text-align: left;
            display: flex;
            flex-direction: column;

        }

        &--info-pay {
            font-family: 'Hind Siliguri', sans-serif;
            margin-top: 0px;
            display: flex;
            flex-direction: row;
            align-items: center;

            &-imgbank {
                width: 30px;
                height: 30px;
                margin-right: 15px;
            }
        }

        &--info-data {
            font-family: 'Hind Siliguri', sans-serif;
            display: flex;
            flex-direction: column;
            margin-top: 15px;

            &-adress,
            &-sum {
                color: #484848;
                font-size: 19px;
            }

        }
    }
}
</style>
